.page-btn {
  @apply inline-block;
  @apply font-bold text-base leading-px22 tracking-em_001;
  @apply rounded-px10;
  @apply py-3 px-6;


  &.primary /* deprecated, use: page-btn-primary */
  , &-primary {
	@apply text-white bg-teal-dark;
  }

  &.secondary /* deprecated, use: page-btn-secondary */
  , &-secondary {
	@apply text-teal-dark bg-white border-2 border-teal-dark;
  }

  @screen xl {
	@apply text-px20 leading-px26;
	@apply py-5 px-20;
  }
}
